.card {
  display: flex;
  padding: 1rem;
  border: 1px solid gainsboro;
  justify-content: space-around;
  align-items: center;
  margin-bottom: 1rem;
  text-decoration: none;
  border-radius: 10px;
}

.icons {
  display: flex;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background: #e4f8ee;
  justify-content: center;
  align-items: center;
  color: #43b8b1;
}
.icons svg {
  font-size: 1.5rem;
}

.text {
  display: flex;
  flex: 0.8;
  flex-direction: column;
  justify-content: center;
  padding: 0.5rem;
}

.text p {
  font-size: 13px;
  color: gray;
}
.text h5 {
  font-size: 20px;
  color: black;
}
