.imageArea {
  display: flex;
  width: 100%;
  max-height: 15rem;
  overflow: hidden;
  border-radius: 10px;
}
.imageArea img {
  width: 100%;
  object-fit: cover;
  object-position: center;
}
.titleArea {
  display: flex;
  padding: 1rem;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.title {
  display: flex;
  flex: 0.6;
  align-items: center;
}
.title h1 {
  font-size: 2rem;
  font-weight: bold;
}
.buttonContainer {
  display: flex;
  flex: 0.4;
  justify-content: flex-end;
}
.firstTitle {
  width: 100%;
  display: flex;
  justify-content: space-around;
}
.secondTitle {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 0.5rem;
}

.capsule {
  display: flex;
  padding: 0.3rem 1rem;
  border-radius: 5px;
  text-align: left;
  font-size: 12px;
  margin-right: 1rem;
  justify-content: center;
  align-items: center;
}
.secondTitle p {
  display: flex;
  font-size: 12px;
  justify-content: center;
  align-items: center;
  margin-right: .5rem;
}
.capsule svg {
  margin-right: 0.3rem;
}
.secondTitle p svg {
  margin-right: 0.3rem;
}
.success {
  background: #e4f8ee;
  color: #43b8b1;
}

.pending {
  background: #f8f5e4;
  color: #eebc34;
}
.section{
    display: flex;
    width: 100%;
    padding: .5rem;
    border-bottom: 1px solid gainsboro;
}
.divider{
    display: flex;
    width: 100%;
    justify-content: space-between;
}
.divOne{
    display: flex;
    flex: 0.2;
    border-right: 1px solid gainsboro;
    flex-direction: column;
}
.divTwo{
    display: flex;
    flex: 0.8;
}