@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500&display=swap");

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
}

:root {
  --teal: #00a19c;
  --toyota: #eb0a1e;
  --lban: #640ad2;
}

.app {
  flex: 1;
  display: flex;
  height: 100vh;
  padding: 0;
  justify-content: center;
  align-items: center;
}

.lms {
  display: flex;
  flex: 0.75;
  height: 100vh;
  /* background: linear-gradient(270deg, rgba(46, 3, 62, 0.8), rgba(0,0,0,0.8)),url('./assets/lms3.webp'); */
  background: url("./assets/lms3.webp");
  padding: 5rem;
  justify-content: center;
  /* align-items: center; */
  flex-direction: column;
  color: white;
}

.lms h1 {
  font-size: 4rem;
}

.lms h4 {
  font-size: 2rem;
}

.auth {
  display: flex;
  flex: 0.25;
  height: 100vh;
  padding: 2rem;
  align-items: center;
}

