.appContainer {
  display: flex;
  flex: 1;
  width: 100%;
  height: 100vh;
  background: linear-gradient(
    90deg,
    rgba(248, 248, 248, 1),
    rgba(252, 247, 234, 1),
    rgba(243, 245, 227, 1),
    rgba(216, 244, 232, 1)
  );
  justify-content: space-around;
  padding: 2rem;
}

.contentsRight {
  flex-grow: 1;
  padding: 0;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
  flex: 0.95;
  padding: 0.5rem 2rem;
  background: white;
  height: calc(100vh - 4rem);
  border-radius: 10px;
}

.dashboardFlex {
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
}

.content {
  width: 100%;
  display: flex;
  flex-basis: 32%;
  border: 1px solid gainsboro;
  border-radius: 10px;
  min-height: 5rem;
  flex-direction: column;
}
.borderless {
  border: none;
}

.btnContainer {
  display: flex;
  justify-content: flex-start;
  margin-bottom: 1rem;
}
.right {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 1rem;
}
.btn {
  height: 3.5rem;
  outline: none;
  padding: 1rem 3rem;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  text-align: center;
  text-decoration: none;
  border: none;
  outline: none;
  align-items: center;
  margin-right: 2rem;
}

.btnSM {
  height: 2.5rem;
  outline: none;
  padding: 1rem 2rem;
  border-radius: 5px;
  display: flex;
  justify-content: center;
  text-align: center;
  text-decoration: none;
  border: none;
  outline: none;
  align-items: center;
  margin-right: 1rem;
}

.btn__white {
  background: white;
  /* box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px; */
  color: #43b8b1;
  border: 1px solid #43b8b1;
}

.btn__teal {
  color: white;
  /* box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px; */
  background: #43b8b1;
}
.btn__gray {
  background: white;
  /* box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px; */
  color: gray;
  border: 1px solid gray;
}

.InputFlex {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  flex: 1;
}

.adult {
  flex-basis: 100%;
}

.child {
  flex-basis: 50%;
}

.listPadding {
  padding: 1rem 2rem;
}

.loading {
  height: 40px;
  width: 40px;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 50%;
  border: 2px solid rgba(0, 0, 0, 0.4);
  border-top-color: #fff;
  border-bottom-color: #fff;
  animation: spinner3 800ms ease infinite;
}

@keyframes spinner3 {
  to {
    transform: rotate(360deg);
  }
}

button:disabled {
  background: whitesmoke;
  color: black;
  cursor: not-allowed;
}

.cardBasis {
  flex-basis: 23%;
}

.video-container-styles {
  width: 60%;
  margin: 0 auto;
}

.playlist-queue-styles {
  box-shadow: 0px 0px 1px white;
  display: flex;
  align-items: center;
  overflow-x: auto;
  background-color: #212836;
}

.playlist-queue-item-styles {
  margin: 5px;
  cursor: pointer;
  min-width: 60px;
  max-width: 60px;
  min-height: 60px;
  max-height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 4px solid transparent;
}

.current-playing-video-styles {
  box-shadow: 0px 0px 2px white;
  transition: 0.2s;
}

.thumbnail-styles {
  width: 100%;
  height: 100%;
}

.video-styles {
  width: 100%;
  height: 500px;
  object-fit: cover;
}

@media screen and (max-width: 549px) {
  .video-container-styles {
    width: 100%;
  }
}
