.card {
  display: flex;
  padding: 1rem;
  border: 1px solid gainsboro;
  justify-content: space-around;
  align-items: center;
  margin-bottom: 1rem;
  text-decoration: none;
  border-radius: 10px;
  flex-basis: 23%;
}

.text {
  display: flex;
  width: 100%;
  flex-direction: column;
  justify-content: center;
  padding: 0.5rem;
}

.text p {
  font-size: 13px;
  color: gray;
}
.text h5 {
  font-size: 20px;
  color: black;
}
