.menubar {
  display: flex;
  padding: 0.5rem;
}

.menu {
  display: flex;
  padding: 1rem 3rem;
  justify-content: center;
  align-items: center;
  background: whitesmoke;
}

.menu:hover {
  background: #43b8b1;
  border-bottom: 2px solid #e9f0f0;
  color: white;
}

.active {
  background: #43b8b1;
  border-bottom: 2px solid #e9f0f0;
  color: white;
}
