.formContainer {
  display: flex;
  flex-direction: column;
  flex: 1;
}

.lg {
  flex: 0.5;
}

.formContainer h3 {
  /* color: var(--lban); */
  font-size: 1.5rem;
  font-weight: bold;
}

.formContainer p {
  margin-bottom: 1rem;
}

.right {
  display: flex;
  justify-content: flex-end;
}

.center {
  display: flex;
  justify-content: center;
}

.btnContainer {
  display: flex;
  padding: 1rem 2rem;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
